import React, { useState, useRef, useEffect } from 'react'
import logo from '../images/logo.svg';
import { FaBars } from 'react-icons/fa';
import { Link } from 'react-router-dom';


export default  function Navbar () {
    const [showLink, setShowLink] = useState(false);

    const toggleLink = () => {
        setShowLink(!showLink);
    }
    const closeLink = () => setShowLink(false);
    const linksRef = useRef(null);
    
    useEffect(() => {
        if (showLink) {
            linksRef.current.classList.add('show-nav');
        } else {
            linksRef.current.classList.remove('show-nav');
        }
    }, [showLink]);
    return (
        <nav className='navbar'>
            <div className='nav-center'>
                <div className='nav-header'>
                    <Link to='/'>
                        <img src={logo} alt='beach resort' />
                    </Link>
                    <button type='button' className='nav-btn' onClick={toggleLink}>
                        <FaBars className='nav-icon' />
                    </button>
                </div>
                <div>
                    <ul ref={linksRef} className='nav-links'>
                        <li>
                            <Link to='/' onClick={closeLink}>Home</Link>
                        </li>
                        <li>
                            <Link to='/rooms' onClick={closeLink}>Rooms</Link>
                        </li>
                    </ul>
                </div>

            </div>
        </nav>
    );
    
}
